import {Box, Button, Dialog, DialogActions, DialogContent, Typography} from '@mui/material';
import Header from 'components/Header';
import produce from 'immer';
import {useLayoutEffect, useState} from 'react';
import HairDrawTicket from './0515.img.05150040.info.icon.png';
import Map from './map.png';
const drawData = [
    {name: '', chance: 1},
    {name: '', chance: 1},
    {name: '', chance: 1},
    {name: '', chance: 6},
    {name: '', chance: 6},
    {name: '', chance: 6},
    {name: '', chance: 6},
    {name: '', chance: 6},
    {name: '', chance: 16.75},
    {name: '', chance: 16.75},
    {name: '', chance: 16.75},
    {name: '', chance: 16.75},
];
function getRandomIntInclusive(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1) + min);
}
const pSum = drawData.reduce((result, value) => {
    return result + value.chance;
}, 0);
const nameFormat = (name = '', subEnd = true) => {
    return name.substring(2, subEnd ? name.length - 2 : name.length);
};
const RenderCount = ({value}) => {
    return (
        <Box
            sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                px: 2,
            }}
        >
            <Typography gutterBottom sx={{mt: 2, fontWeight: '600'}}>
                {nameFormat(value.name)}
            </Typography>
            <Box
                component="img"
                sx={{objectFit: 'contain', height: 75, mt: 2}}
                src={`${process.env.PUBLIC_URL}/hairData/${value.name}.png`}
            />
            <Typography
                sx={{
                    fontWeight: '700',
                    fontSize: 32,
                    mt: 1,
                    ...(value.count > 0 ? {color: '#ed6d03'} : {}),
                }}
            >
                {value.count ? value.count : 0}
            </Typography>
        </Box>
    );
};

const HairVirtualDrawScreen = () => {
    const [data, setData] = useState(drawData);
    const [nowDrawItem, setNowDrawItem] = useState({});
    const [resultList, setResultList] = useState([]);
    const [alertVisible, setAlertVisible] = useState(false);
    useLayoutEffect(() => {
        document.title = '';
    }, []);
    const draw = () => {
        let tempPsum = pSum;

        for (let i = 0; i < drawData.length; i++) {
            const randNum = getRandomIntInclusive(1, tempPsum);
            const thisData = drawData[i];
            if (randNum <= thisData.chance) {
                setResultList(
                    produce((draft) => {
                        draft.push(thisData);
                    })
                );
                setNowDrawItem(thisData);
                setData(
                    produce((draft) => {
                        draft[i].count = (draft[i].count || 0) + 1;
                    })
                );
                if (thisData.chance < 3) {
                    setAlertVisible(true);
                }
                return {...thisData, index: i};
            } else {
                tempPsum = tempPsum - thisData.chance;
            }
        }
    };

    return (
        <Box sx={{flexGrow: 1, display: 'flex', flexDirection: 'column', height: 2000}}>
            <Header>
                <Box component="img" src={HairDrawTicket} sx={{height: 30, width: 30}} />
                <Typography variant="h6" component="div" sx={{flexGrow: 1, ml: 1}}>
                    
                </Typography>
            </Header>
            <Box
                sx={{
                    backgroundSize: 'cover',
                    backgroundRepeat: 'no-repeat',
                    backgroundImage: `url(${Map})`,
                    width: '100%',
                    maxWidth: 900,
                    height: 500,
                    backgroundPosition: '65%',
                    position: 'relative',
                    alignSelf: 'center',
                }}
            >
                <Box
                    sx={{
                        position: 'absolute',
                        left: 0,
                        right: 0,
                        bottom: 120,
                        display: 'flex',
                        justifyContent: 'center',
                    }}
                >
                    <Box
                        component="img"
                        sx={{objectFit: 'contain', transform: 'scale(1.2)'}}
                        src={`${process.env.PUBLIC_URL}/hairData/${nowDrawItem.name}.png`}
                    />
                </Box>
            </Box>
            <Box sx={{display: 'flex', flexDirection: 'column', alignItems: 'center', pt: 4}}>
                <Box
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'start',
                        alignSelf: 'center',
                    }}
                >
                    <Button color="inherit" sx={{px: 2, py: 1}} onClick={draw}>
                        <Box component="img" src={HairDrawTicket} sx={{height: 30, width: 30}} />
                        <Typography sx={{fontWeight: '600', ml: 2}}></Typography>
                    </Button>
                    <Button
                        color="inherit"
                        sx={{px: 2, py: 1}}
                        onClick={() => {
                            for (let i = 0; i < 10; i++) {
                                const result = draw();
                                if (result.chance < 3) {
                                    break;
                                }
                            }
                        }}
                    >
                        <Box component="img" src={HairDrawTicket} sx={{height: 30, width: 30}} />
                        <Typography sx={{ml: 2, fontWeight: '600'}}>(10)</Typography>
                    </Button>
                    <Button
                        sx={{px: 2, py: 1, fontWeight: '600', fontSize: 16}}
                        color="inherit"
                        onClick={() => {
                            setResultList([]);
                            setData(drawData);
                        }}
                    >
                        
                    </Button>
                    <Box sx={{px: 2, py: 1, display: 'flex', alignItems: 'center'}}>
                        <Typography sx={{fontWeight: '600'}}></Typography>
                        <Box component="img" src={HairDrawTicket} sx={{height: 30, width: 30, ml: 2}} />
                        <Typography sx={{mx: 2, fontWeight: '600'}}>x</Typography>
                        <Typography sx={{fontWeight: '600'}}>{resultList.length}</Typography>
                    </Box>
                </Box>
                <Box
                    sx={{
                        width: '100%',
                        maxWidth: 500,
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-around',
                    }}
                >
                    {data
                        .filter((value) => value.chance < 3)
                        .map((value) => (
                            <RenderCount key={value.name} value={value} />
                        ))}
                </Box>
                <Box
                    sx={{
                        width: '100%',
                        maxWidth: 500,
                        display: 'flex',
                        flexDirection: 'row',
                        flexWrap: 'wrap',
                        alignItems: 'end',
                    }}
                >
                    {data
                        .filter((value) => value.chance >= 3)
                        .map((value) => (
                            <RenderCount key={value.name} value={value} />
                        ))}
                </Box>
            </Box>
            <Dialog open={alertVisible} onClose={() => {}} maxWidth="xs" fullWidth>
                <DialogContent sx={{alignItems: 'center', display: 'flex', flexDirection: 'column'}}>
                    <Typography sx={{fontWeight: '700', fontSize: 30}} gutterBottom>
                        
                    </Typography>
                    <Typography sx={{fontWeight: '700'}} gutterBottom>
                        {resultList.length}
                    </Typography>
                    <Typography sx={{fontWeight: '700'}} gutterBottom>
                        {nameFormat(nowDrawItem.name, false)}
                    </Typography>
                    <Box
                        component="img"
                        sx={{objectFit: 'contain', height: 120, my: 2, userSelect: 'none'}}
                        src={`${process.env.PUBLIC_URL}/hairData/${nowDrawItem.name}.png`}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setAlertVisible(false)} color="inherit">
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};
export default HairVirtualDrawScreen;
