import {Box, FormControl, FormControlLabel, Radio, RadioGroup} from '@mui/material';
import useDrawData from './useDrawData';
import useZState from './useZState';
const SetDataKeyAndShowChance = () => {
    const [nowDataKey, setNowDataKey] = useZState('nowDataKey'); // or /
    const drawData = useDrawData();
    return (
        <Box sx={{display: 'flex'}}>
            <Box sx={{display: 'flex', alignItems: 'flex-end', flexDirection: 'column', flex: 1, mr: 3}}>
                <FormControl>
                    <RadioGroup
                        value={nowDataKey}
                        onChange={(event) => {
                            setNowDataKey(event.target.value);
                        }}
                    >
                        <FormControlLabel value="weaponData" control={<Radio />} label="" />
                        <FormControlLabel value="armorData" control={<Radio />} label="/" />
                        {nowDataKey === '' && <FormControlLabel value="" control={<Radio />} label="" />}
                    </RadioGroup>
                </FormControl>
            </Box>
            <Box sx={{display: 'flex', alignItems: 'flex-start', flexDirection: 'column', flex: 1}}>
                <table>
                    <thead>
                        <tr>
                            <td></td>
                            <td align="right"></td>
                        </tr>
                    </thead>
                    <tbody>
                        {drawData.map(({id, add, chance, count}, index) => (
                            <tr key={index}>
                                <td>+{add}</td>
                                <td align="right">{chance}%</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </Box>
        </Box>
    );
};
export default SetDataKeyAndShowChance;
